import React, { Component } from "react";
import { Link } from 'react-router-dom';
import moment from 'moment'
import {Line, defaults} from 'react-chartjs-2';
import '../styles/index.css';
defaults.global.maintainAspectRatio = false

class EventAnalytics extends Component {


    render() {


        var events = JSON.parse(localStorage.getItem("allEvents"));
        events = events.sort((a,b) => moment(a.date + ' ' + a.time).diff(b.date + ' ' + b.time));                   
        
        var valenceValues = [];
        var arousalValues = [];
        var labels = [];
        events.map(e => {
            valenceValues.push(e.valence);
            arousalValues.push(e.arousal);
            labels.push([moment(e.date).format('DD/MM') + '\n' + e.time, e.eventName])
        });

        var valenceDataSet = {
            backgroundColor: '#87ceeb',            
            borderColor: '#45b3e0',
            data: valenceValues,
            label: 'Valence'
        };
        var arousalDataSet = {     
            borderColor: '#FE0101',
            data: arousalValues,
            label: 'Arousal'
        };

        const data = {
            labels: labels,
            datasets: [arousalDataSet, valenceDataSet]
        };
        const options = {
            scales: {
                xAxes: [{
                  ticks: {
                    fontSize: 8,
                    autoSkip: true,
                    maxTicksLimit: 6,
                    maxRotation: 0,
                    minRotation: 0
                  }
                }]
              }
        }
        console.log('data', data);
        return (
            <div>
                <main role="main" className="container">
                    <div className="body my-3 p-3 text-center bg-light rounded shadow-sm">
                        <h2 className="sticky-top bg-light pb-2 mb-0">
                            Affective State Profile App
                        </h2>
                        <article className="canvas-container">
                            <Line data={data} options={options}/>
                        </article>
                        <br />
                        <br />
                        <br />
                        <Link to="/">
                            <button type="button" className="mx-auto btn btn-warning btn-lg btn-block">Back to Home</button>
                        </Link>
                        <br />
                        <br />
                    </div>
                </main>
            </div>
        );
    }
}

export default EventAnalytics;



