import React, { Component } from "react";
import { Link } from 'react-router-dom';

class AffectiveStateLogging extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            mood: 0,
            eventName: '',
            eventDetails: '',
            date: '',
            time: '', 
            valence: 0, 
            arousal: 0 
        };
      }

    myChangeHandler = (event) => {
        let nam = event.target.name;
        let val = event.target.value;
        this.setState({[nam]: val});
    }

    addEvent = (event) =>{
        var existingEvents = JSON.parse(localStorage.getItem("allEvents"));
        existingEvents.push(event);
        localStorage.setItem("allEvents", JSON.stringify(existingEvents));
    }

    saveEvent = (e) => {
        e.preventDefault();
        console.log(this.state);
        this.addEvent(this.state);
        window.location.hash = '/'
    }

    render() {
        return (
            <div>
                <main role="main" className="bg-light container-fluid">
                    <div className="body my-3 p-3 text-center bg-light rounded shadow-sm">
                        <h2 className="sticky-top bg-light pb-2 mb-0">
                            Affective State Logging
                        </h2>
                    </div>
                    <form onSubmit={this.saveEvent}>
                        <div class="mb-3">                            
                            <label class="form-check-label" for="eventName">Event Name</label>
                            <input type="text" onChange={this.myChangeHandler} class="form-control" name="eventName" id="eventName" required/>
                            <div class="invalid-feedback">
                                Please enter an event name in the textarea.
                            </div>
                        </div>
                        <div class="mb-3">                            
                            <label class="form-check-label" for="eventDetails">Event Details</label>
                            <textarea onChange={this.myChangeHandler} rows='3' class="form-control" name="eventDetails" id="eventDetails" required/>
                            <div class="invalid-feedback">
                                Please enter an event name in the textarea.
                            </div>
                        </div>
                        <div class="mb-3">
                            <label for="eventDate" class="form-label">Date</label>
                            <input id="eventDate" onChange={this.myChangeHandler} type="date" name="date" class="form-control" required/>
                            <div class="invalid-feedback">
                                Please enter a date.
                            </div>
                        </div>
                        <div class="mb-3">
                            <label for="eventTime" class="form-label">Time</label>
                            <input type="time" onChange={this.myChangeHandler} name="time" class="form-control" required/>
                            <div class="invalid-feedback">
                                Please enter a time.
                            </div>
                        </div>
                        <div class="form-group">
                            <label for="mood" class="form-label">General Mood</label> <br />
                            <select onChange={this.myChangeHandler} class="form-select form-select-sm" size="3" id="mood" name="mood" aria-label=".form-select-sm example" required>
                                <option value="1">Sad</option>
                                <option value="2">Moderate</option>
                                <option value="3">Happy</option>
                            </select>
                        </div>
                        <div class="mb-3">
                            <label for="valence" class="form-label">Valence Level</label> <br />
                            <div class="col-xs-6 col-xs-offset-3">
                                <input type="range" style={{width: '100% !important'}} onChange={this.myChangeHandler} value={this.state.valence} name="valence" class="form-range" min="-10" max="10" id="valence" required/>
                            </div>

                            <div class="invalid-feedback">
                                Please enter a level of valence.
                            </div>
                        </div>

                        <div class="mb-3">
                            <label for="arousal" class="form-label">Arousal Level</label><br />
                            <div class="col-xs-6 col-xs-offset-3">
                                <input type="range" style={{width: '100% !important'}} onChange={this.myChangeHandler} value={this.state.arousal} name="arousal" class="form-range" min="-10" max="10" id="arousal" required/>
                            </div>
                            <div class="invalid-feedback">
                                Please enter a level of arousal.
                            </div>
                        </div>
                        <button type="submit" className="mx-auto btn btn-primary btn-lg btn-block">Submit</button>
                        <br />
                    </form>
                    <Link to="/">
                        <button type="button" className="mx-auto btn btn-warning btn-lg btn-block">Cancel</button>
                    </Link>
                </main>
            </div>
        );
    }
}

export default AffectiveStateLogging;



