import React, { Component } from "react";
import { Link } from 'react-router-dom';
import PastEventCards from '../components/PastEventCards';
import moment from 'moment';

class PastEvents extends Component {
    render() {
        var events = JSON.parse(localStorage.getItem("allEvents"));
        return (
            <div>
                <main role="main" className="bg-light container-fluid">     
                    <div className="body my-3 p-3 text-center bg-light rounded shadow-sm">
                        <h2 className="fixed-top bg-light pb-2 mb-0">
                            Past Events
                        </h2>
                    </div>
                    <br />
                    {
                    console.log('events', events),
                    events = events.sort((a,b) => moment(a.date + ' ' + a.time).diff(b.date + ' ' + b.time)),                   
                    console.log('eventsSorted', events),
                    events.map((element) => {
                        console.log("entered");
                        return (<PastEventCards card={element}></PastEventCards>)
                    })}
                    <br />
                    <br />
                    <Link to="/">
                        <button type="button" className="mx-auto btn btn-warning btn-lg btn-block">Back to Home</button>
                    </Link>
                    <br />
                    <br />
                    <br />
                </main>
            </div>
        );
    }
}

export default PastEvents;



