import React, { Component } from "react";
import { Link } from 'react-router-dom';
import happy from '../assets/images/happy.png'

class App extends Component {
    render() {
        return (
            <div>
                <main role="main" className="container">
                    <div className="body my-3 p-3 text-center bg-light rounded shadow-sm">
                        <h2 className="sticky-top bg-light pb-2 mb-0">
                            Affective State Profile App
                        </h2>
                        <div className="text-center">
                            <img alt="" src={happy} className="rounded mx-auto" width="150vw" height="150vw"></img>
                        </div>
                        <br />
                        <div class="d-grid gap-2 col-12 mx-auto">
                            <Link to="/eventLog">
                                <button type="button" className="mx-auto btn btn-primary btn-lg btn-block">Affective State Logging</button>
                            </Link>
                        </div>
                        <br />
                        <div class="d-grid gap-2 col-12 mx-auto">
                            <Link to="/pastEvents">
                                <button type="button" className="mx-auto btn btn-primary btn-lg btn-block">Past Events</button>
                            </Link>              
                        </div>
                        <br />
                        <div class="d-grid gap-2 col-12 mx-auto">
                            <Link to="/eventAnalytics">
                                <button type="button" className="mx-auto btn btn-primary btn-lg btn-block">Affective State Logging</button>
                            </Link>
                            </div>
                        <br />
                    </div>
                </main>
            </div>
        );
    }
}

export default App;



