import React, { Component } from "react";
import moment from 'moment';

class PastEventCards extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            mood: props.card.mood,
            eventName: props.card.eventName,
            eventDetails: props.card.eventDetails,
            date: props.card.date,
            time: props.card.time, 
            valence: props.card.valence, 
            arousal: props.card.arousal 
        };
      }

    render() {
        console.log('state',this.state);
        return (
            <div>
                <div class="card" >
                    <div class="card-body">
                    <h5 class="card-title">{this.props.card.eventName}</h5>
                    <span>
                        <h6 class="card-subtitle mb-2 text-muted">{moment(this.props.card.date).format('dddd, MMMM Do') + ' - ' + this.props.card.time} </h6>
                    </span>
                    <p class="card-text">{this.props.card.eventDetails}</p>
                    <label for="valence" class="form-label">Valence Level</label> <br />
                    <input type="range" value={this.props.card.valence} disabled name="valence" class="form-range" min="-10" max="10" id="valence" required/>
                    <br />
                    <label for="valence" class="form-label">Arousal Level</label> <br />
                    <input type="range" value={this.props.card.arousal} disabled name="arousal" class="form-range" min="-10" max="10" id="arousal" required/>

                    </div>
                </div>
                <br />
            </div>
        );
    }
}

export default PastEventCards;


